<template>
  <div class="orderItemFooter">
    <div v-if='status == 3 || status == 6' class='btn' @click.stop='checkLogistics(order_sn)'></div>
    <div class='btn' v-if='status == 3' @click='getGoods(order_sn)'></div>
    <div v-if='status != 0 && status !=7 && status != 2' class='btn' @click.stop='returnGoods(order_sn)'></div>
    <div v-if='status == 6 || status == 7 || status == 2' class='btn' @click.stop='delOrder(order_sn)'></div>
    <div v-if='status == 0' class='btn' @click.stop='unpay(order_sn)'></div>
    <div v-if='status == 6' class='btn' @click.stop='evaluation(order_sn)'></div>
    <div class='btn' v-if='status == 0' @click='gopay(order_sn)'></div>
    
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { MessageBox } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'orderItemFooter',
  props:{
    order_sn:String,
    status:String
  },
  data () {
    return {
			
    }
  },
  created(){
		
	},
	components:{
		
	},
	methods:{
		go(){
      this.$emit('gologInformation','')
    },
    /**
     * 
    */
    gopay(order_sn){
      Indicator.open('Loading...');
      let data = {
        type:1
      }
      this.axios({
        url:`shopGoPay/${order_sn}`,
        method:'post',
        data:this.qs.stringify(data)
      }).then(res => {
        Indicator.close();
        if(res.data.status == 200){
          wx.chooseWXPay({
            timestamp: res.data.data.timeStamp, // jssdktimestamptimeStampS
            nonceStr: res.data.data.nonceStr, //  32 
            package: res.data.data.package, // prepay_id
            signType: res.data.data.signType, // 'SHA1''MD5'
            paySign: res.data.data.paySign, // 
            success: res => {
              this.$router.replace({name:'order'})
            }
          })
        }else{
          Toast(res.data.message);
        }
      })
    },
    /**
     * 
    */
    checkLogistics(order_sn){
      this.$router.push({name:"logisticsInformation",query:{order_sn:order_sn}})
    },
    /**
     * 
    */
    returnGoods(order_sn){ 
      this.$router.replace({name:'applicationForDrawback',query:{order_sn:order_sn}})
    },
    /**
     * 
    */
    getGoods(order_sn){
      MessageBox.confirm('?').then(action => {
        let data = {
          order_sn: order_sn
        }
        this.axios({
          url:'shopOrderGoods',
          method:'put',
          data:this.qs.stringify(data)
        }).then(res => {
          if(res.data.status == 200){
            Toast('');
            this.$emit('getData','aaa')
          }else{
            Toast(res.data.message);
          }
          
        })
      });
    },
    /**
     * 
    */
    unpay(order_sn){
      MessageBox.confirm('?').then(action => {
        this.axios({
          url:`shopUnOrder/${order_sn}`,
          method:'put'
        }).then(res => {
          if(res.data.status == 200){
            this.$emit('getData','aaa')
          }else{
            Toast(res.data.message);
          }
        })
      });
    },
    /**
     * 
    */
    delOrder(order_sn){
      MessageBox.confirm('?').then(action => {
        Indicator.open('Loading...');
        this.axios({
          url:`shopOrder/${order_sn}`,
          method:'delete'
        }).then(res => {
          console.log(res);
          Indicator.close();
          if(res.data.status == 200){
            Toast('');
            this.$router.replace({name:"order"})
          }else{
            Toast(res.data.message);
          }
        })
      });
    }
	},
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.orderItemFooter{
  position:fixed;
  bottom:0vw;
  left:0vw;
  width:100vw;
  display:flex;
  justify-content:flex-end;
  align-items:center;
  border-top:1px solid #eee;
  background:#fff;
  
  .btn{
    padding:2vw 4vw;
    border-radius:5vw;
    font-size:3vw;
    margin:2vw;
    border:1px solid #bbb;
    color:#555;
  }
  
}

</style>



