import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index/index'  //
import classification from '@/components/classification/classification'  //
import my from '@/components/my/my'   //
import goodsDetails from '@/components/goodsDetails/goodsDetails'   //
import shoppingCart from '@/components/shoppingCart/shoppingCart'   //
import address from '@/components/address/index'    //
import order from '@/components/order/order'    //
import classificationDetails from '@/components/classificationDetails/classificationDetails'    //
import comments from '@/components/comments/comments'   //
import commentsDetail from '@/components/commentsDetail/commentsDetail'   //
import createOrder from '@/components/createOrder/createOrder'   //
import coupons from '@/components/coupons/coupons'   //
import search from '@/components/search/search'   //
import payOk from '@/components/payOk/payOk'   //
import orderItem from '@/components/orderItem/orderItem'   //
import logisticsInformation from '@/components/logisticsInformation/logisticsInformation'   //
import applicationForDrawback from '@/components/applicationForDrawback/applicationForDrawback'   //()
import returnOrder from '@/components/returnOrder/returnOrder'   //


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },{
      path: '/classification',
      name: 'classification',
      component: classification
    },{
      path: '/my',
      name: 'my',
      component: my
    },{
      path: '/goodsDetails',
      name: 'goodsDetails',
      component: goodsDetails
    },{
      path: '/shoppingCart',
      name: 'shoppingCart',
      component: shoppingCart
    },{
      path: '/address',
      name: 'address',
      component: address
    },{
      path: '/order',
      name: 'order',
      component: order
    },{
      path: '/classificationDetails',
      name: 'classificationDetails',
      component: classificationDetails
    },{
      path: '/comments',
      name: 'comments',
      component: comments
    },{
      path: '/commentsDetail',
      name: 'commentsDetail',
      component: commentsDetail
    },{
      path: '/createOrder',
      name: 'createOrder',
      component: createOrder
    },{
      path: '/coupons',
      name: 'coupons',
      component: coupons
    },{
      path: '/search',
      name: 'search',
      component: search
    },{
      path: '/payOk',
      name: 'payOk',
      component: payOk
    },{
      path: '/orderItem',
      name: 'orderItem',
      component: orderItem
    },{
      path: '/logisticsInformation',
      name: 'logisticsInformation',
      component: logisticsInformation
    },{
      path: '/applicationForDrawback',
      name: 'applicationForDrawback',
      component: applicationForDrawback
    },{
      path: '/returnOrder',
      name: 'returnOrder',
      component: returnOrder
    }
  ]
})



